






























import { Component, Prop, Vue } from 'vue-property-decorator';
import MineField from './components/MineField.vue';
import DifficultyForm from './components/DifficultyForm.vue';
import Timer from './components/Timer.vue';

enum State {
  New,
  Ingame,
  Win,
  Lose,
}

@Component({
  components: {
    DifficultyForm,
    Timer,
    MineField,
  },
})
export default class App extends Vue {
  public rows: number = 16;
  public cols: number = 30;
  public mines: number = 99;

  public state: State = State.New;
  public result: string = '';

  public gameID: number = 1;
  private flaggedMines: number = 0;

  public handleNew(rows: number, cols: number, mines: number) {
    if (rows < 3 || cols < 3 || mines < 1) {
      return;
    }

    this.rows = rows;
    this.cols = cols;
    this.mines = mines;

    this.state = State.New;
    this.result = '';

    this.flaggedMines = 0;
    this.gameID += 1;
  }

  public handleIngame() {
    this.state = State.Ingame;
  }

  public handleWin() {
    this.state = State.Win;
    this.result = 'Win!';
    this.stopTimer();
  }

  public handleLose() {
    this.state = State.Lose;
    this.result = 'Game over!';
    this.stopTimer();
  }

  public handleFlaggedMines(flaggedMines: number) {
    this.flaggedMines = flaggedMines;
  }

  private stopTimer() {
    // @ts-ignore
    this.$refs.timer.stop();
  }

  get isIngame() {
    return this.state === State.Ingame;
  }

  get isLoss() {
    return this.state === State.Lose;
  }

  get timerKey() {
    return this.state !== State.New;
  }

  get minesLeft() {
    return this.mines - this.flaggedMines;
  }
}
